.LadoEsquerdo {
    display: flex;
    flex-direction: column;
}

.logo {
    margin: 20px 20px;
}

.menu-principal {
    display: flex;
    flex-direction: column;
    margin: 20px 20px;
    height: 60hv;
    justify-content: space-around;
}

.menu-principal span {
    font-weight: bold;
    margin: 10px 0 10px 0;
}

.menu {
    display: flex;
    flex-direction: column;
    padding-top: 30px;
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0 10px 0;
}

.logo img {
    max-width: 75px;
}

.icone {
    color: #000;
    font-size: 30px;
    padding: 10px;
}

.icone svg {
    width: 30px;
}